<template>
    <div>
      <div class="header">
          <div class="back" @click="back">
            <svg
              t="1665994572394"
              class="icon"
              viewBox="0 0 1024 1024"
              version="1.1"
              xmlns="http://www.w3.org/2000/svg"
              p-id="3557"
              width="200"
              height="200"
            >
              <path
                d="M710.4 838.4 358.4 492.8c-12.8-12.8-32-12.8-44.8 0l0 0c-12.8 12.8-12.8 32 0 44.8l352 352c12.8 12.8 32 12.8 44.8 0l0 0C723.2 876.8 723.2 851.2 710.4 838.4z"
                p-id="3558"
                fill="#ffffff"
              ></path>
              <path
                d="M358.4 531.2l352-352c12.8-12.8 12.8-32 0-44.8l0 0c-12.8-12.8-32-12.8-44.8 0L313.6 486.4c-12.8 12.8-12.8 32 0 44.8l0 0C326.4 544 345.6 544 358.4 531.2z"
                p-id="3559"
                fill="#ffffff"
              ></path>
            </svg>
          </div>
          <div class="title">收藏列表</div>
        </div>
      <div class="all">
        <div class="selectlist" v-for="(item, index) in history" :key="index">
          <div class="listbox" @click="xq(item._id)">
            <div class="nr" v-if="item.imageSrc.length === 1">
              <div class="listtitle">{{ item.title }}</div>
              <div class="listimg">
                <img :src="item.imageSrc[0]" alt="" />
              </div>
            </div>
            <div class="nrs" v-else-if="item.imageSrc.length > 1">
              <div class="listtitle">{{ item.title }}</div>
              <div class="listimgs">
                <div v-for="(i, index) in item.imageSrc" :key="index">
                  <img :src="i" alt="" />
                </div>
              </div>
            </div>
            <div class="nrss" v-else>
              <div class="listtitle">{{ item.title }}</div>
            </div>
            <div class="bigfoot">
              <div class="listfoot">
                <div>{{ item.author }}</div>
                <div>{{ item.comment }}评论</div>
                <div>{{ ontime(item.time)}}</div>
              </div>
              <div style="margin-right: 10px"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
      
    </template>
    
    <script>
    import axios from "axios";
    import { Toast } from 'vant';
    export default {
      data() {
        return {
          uid: "",
          history: [],
        };
      },
      created() {
        this.refash()
      },
      methods: {
        back() {
          this.$router.push("/my");
        },
        xq(id) {
          // console.log(id);
          this.$router.push({
            name: "lcontent",
            params: {
              id,
            },
          });
        },
        refash() {
          this.uid = localStorage.getItem("uid");
          axios({
            method: "post",
            url: `https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_fav_list`,
            data:{
                user_id:this.uid,
                skip:0,
                limit:20
            }
          }).then((res) => {
            // console.log(res);
            // console.log(this.uid);
            this.history = res.data.data;
            this.history.reverse()
          });
        },
        
      },
    };
    </script>
    
    <style scoped>
    .header {
      background-color: rgb(7,193,96);
      height: 45px;
      line-height: 45px;
      display: flex;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
    }
    .back {
      /* border: 1px solid red; */
      height: 45px;
      width: 30px;
      margin-left: 13px;
      line-height: 53px;
    }
    .icon {
      height: 20px;
      width: 20px;
    }
    .title {
      /* border: 1px solid red; */
      color: white;
      width: 290px;
      text-align: center;
    }
    svg {
      width: 28px;
      height: 28px;
    }
    .selectlist {
      border-bottom: 1px solid #efefef;
      margin: 10px 0;
      padding-left: 10px;
    }
    .listtitle {
      /* border: 1px solid red; */
      width: 200px;
      margin-left: 3px;
      font-weight: bold;
      font-size: 16px;
      padding: 0 0 20px 0;
    }
    .listimg img {
      width: 150px;
      margin-right: 20px;
    }
    .listimgs {
      /* border: 1px solid red; */
      display: flex;
      justify-content: left;
    }
    .listbox {
      border-bottom: 1px solid #efefef;
      /* padding-left: 10px; */
    }
    .listimgs img {
      width: 111px;
      margin: 2px 5px;
    }
    .listfoot {
      /* border: 1px solid blue; */
      display: flex;
      justify-content: left;
      align-items: center;
      font-size: 14px;
      color: #666;
    }
    .all{
      margin-top: 55px;
    }
    .listfoot div {
      margin-left: 5px;
      width: 110px;
      /* border: 1px solid red; */
      word-break: break-all;
    }
    .nr {
      display: flex;
      justify-content: space-between;
      margin-right: 10px;
    }
    
    .bigfoot {
      display: flex;
      /* border: 1px solid red; */
      justify-content: space-between;
      margin: 5px 0;
      /* align-items: center; */
    }
    </style>